// Большие девайсы (большие десктопы, < 1200px)
@media (max-width: 1199.98px) {
    .advanced-search__form {
        padding: 55px 60px 0;
    }

    .search-form__input {
        max-width: 42.5%;
    }
}

// Средние девайсы («таблетки», < 992px) 
@media (max-width: 991.98px) {
    .search-form__select {
        margin-bottom: 40px;
    }

    .search-form__fieldset-price {
        margin-bottom: 40px;
    }
}

// Малые девайсы («ландшафтные», < 768px)
@media (max-width: 767.98px) {
    .home__advanced-search {
        margin-top: 0;
    }

    .advanced-search__form {
        padding: 30px 0 0;
    }

    .search-form__select {
        margin-bottom: 30px;
    }

    .search-form__fieldset-price {
        margin-bottom: 30px;
    }
}

// Экстрамалые девайсы («телефоны», < 576px)
@media (max-width: 575.98px) {
    .search-form__tab-icon {
        width: 34px;
        height: 34px;
    }
    .search-form__tab-name {
        margin: 0 10px;
    }

    .search-form__tab {
        font-size: 1.6rem;
    }
}

@media (max-width: 400px) {
    .search-form__tab-icon {
        display: none;
    }
}
